import { Container } from '@dojo/widget-core/Container';

import { Content } from './../widgets/Content';

function getProperties(context: any, properties: any) {
	const { category = 'top', page = 1 } = properties;
	if (category !== context.category || page !== context.page) {
		context.fetchStories(category, page);
	}

	return {
		data: context.data,
		pageNumber: parseInt(context.page, 10)
	};
}

export const ContentContainer = Container(Content, 'state', { getProperties });



